.header{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
}
ul {
    display: flex;
    flex-direction: row;
    list-style: none;
}
.Link{
    margin-right: 1rem;
    font-size: 1.5rem;
    font-weight: 400;
    text-decoration: none;
    color: black;
}
.Link:hover{
    font-weight: 800;
}

.logo{
    font-size: 3rem;
}


.Home{
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
.h2{
    -webkit-animation: colorChange infinite 6s;
            animation: colorChange infinite 6s;
    font-size: 4rem;
    text-align: left;
    width: 30vw;
    margin: 0;
}
p{
    font-size: 2rem;
    text-align: left;
    width: 30vw;

}
.HomeSpan {
    -webkit-animation: colorChange infinite 6s ;
            animation: colorChange infinite 6s ;
    padding-left: .5 rem;
}
.HeroImage{
    margin-top: 2rem;
    height: 35vh;
    width: 20vw;
    border: 4px solid black;
    border-radius: 2%;
}

.HomeSection{
    padding-left: 1rem;
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-items: center;
    padding: 0;
    margin: 0;
}


@-webkit-keyframes colorChange {
    0% {
        color: red;
    }
    33% {
        color: green;
    }
    66%{
        color: blue;
    }
    100%{
        color: red;
    }

}


@keyframes colorChange {
    0% {
        color: red;
    }
    33% {
        color: green;
    }
    66%{
        color: blue;
    }
    100%{
        color: red;
    }

}
.about{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.aboutText{
    font-size: 1.5rem;
}
.about-image{
    border-radius: 50%;
    width: 20rem;
    height: 20rem;
    border: 4px solid black;
}
.AboutSection{
    width: auto;
    flex-direction: row;
    display: flex;
    align-items: center;
    justify-content: center;

}
.content{
    display: flex;
    flex-direction: column;
    padding-left: 10rem;
    align-items: center;
}
.aboutHeader{
    font-size: 2rem;
    text-align: left;
    width: 100%;
    margin: 0;
}
.aboutSpan{
   background-image: linear-gradient(
     -225deg,
     #66646d 0%,
     #454249 29%,
     #eb4a0f 67%,
     #e1b514 100%
   );
   background-size: auto auto;
   background-clip: border-box;
   background-size: 200% auto;
   color: #fff;
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   -webkit-animation: textclip 2s linear infinite;
           animation: textclip 2s linear infinite;
 }
/* Animation */

.animate-charcter
{
   text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #66646d 0%,
    #454249 29%,
    #eb4a0f 67%,
    #e1b514 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: textclip 2s linear infinite;
          animation: textclip 2s linear infinite;
  display: inline-block;
      font-size: 190px;
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}


.skillsHeader{
    text-align: left;
    font-size: 2rem;
}

.SkillsContainer{
    display: flex;
    width: 70vw;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
}
.skill{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 1rem;
    border-radius: 10%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 1rem;

}
.skillName{
    padding-left: 1rem;
    font-size: 1.2rem;
    margin: 0;
    padding-left: 1rem;
    width: auto;
}


.HobbiesHeader{
    text-align: left;
    font-size: 2rem;
}

.HobbiesContainer{
    display: flex;
    width: 70vw;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
}
.Hobbie{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 1rem;
    border-radius: 10%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 1rem;

}
.HobbieName{
    padding-left: 1rem;
    font-size: 1.2rem;
    margin: 0;
    padding-left: 1rem;
    width: auto;
}

.projectContainer{
    display: flex;
    flex-direction: column;
    border-bottom:1px solid #bab8b8;
    align-self: center;
    padding: 3vh;
    margin: 5vh;
}
.projectContainer h2{
    font-size: 5vh;
}
.projectDetails{
    display: flex;
    justify-content: space-evenly;
}

.projectImage{
    max-width: 50vw;
    max-height: 40vh;
}
.techList{
    display: flex;
    flex-direction: column;
}
.techList li{
    padding-bottom: 3vh;
    font-size: 3vh;
}

.projectParagraph{
    width: auto;
    align-self: center;
    font-size: 3vh;
}


.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-float infinite 3s ease-in-out;
            animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@-webkit-keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes App-logo-float {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.Contact{
    padding-top: 2rem;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    margin-bottom: 5vh;
}
.contactLabel{
    font-size: 2rem;
    text-align: left;
    justify-content: center;
    width: 70vw;
}
a{
    padding-right: 1.5rem;
    font-size: 1.2rem;
    text-decoration: none;
    color: black;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

